import { FunctionalComponent, h } from '@stencil/core';

export type FcInputLabelType = {
  id?: string;
  htmlForId: string;
  label: string;
  required?: boolean;
  showRequired?: boolean;
  tooltipText?: string;
  tooltipWidth?: number;
};

export const FcInputLabel: FunctionalComponent<FcInputLabelType> = props => {
  const tooltipWidth = props.tooltipWidth || 0;

  return (
    <div class="ath-input__label">
      <label id={props.id} htmlFor={props.htmlForId} class="ath-input__label__wrapper">
        <span>{props.label}</span>
        {props.required && props.showRequired && (
          <span aria-hidden="true" class="required">
            *
          </span>
        )}
      </label>

      {!!props.tooltipText && (
        <ath-tooltip headingText={props.tooltipText} position="top" has-arrow="true" color="primary" max-width={tooltipWidth}>
          <ath-tooltip-trigger
            icon="info"
            size="md"
            aria-label={props.tooltipText}
            onClick={ev => ev.stopPropagation()}
            onKeyDown={ev => ev.stopPropagation()}
            aria-describedBy={props.htmlForId}
          ></ath-tooltip-trigger>
        </ath-tooltip>
      )}
    </div>
  );
};
